import React from "react";
import Post from "./post";
import http from "~/http";
import { FormatBlog, FormatListBlog } from "~/domain/blog";
import Head from "next/head";
import Style from "./style.module.scss";
import { inject, observer } from "mobx-react";
import { getHost, SITE_NAME } from "~/utils";
import { GiscusComment } from "~/components/GiscusComment";
import { Request } from "~/http/request";
import Operator from "./components/Operator";
import { withRouter } from "next/router";
import { LatestArticles } from "./components/LatestArticles";
import Script from "next/script";

@inject("productStore")
@observer
class Blog extends React.Component {

  render() {
    const { data, url, latestArticleList = [], router } = this.props;
    if (!data) {
      return null;
    }
    const postLink = getHost() + router.asPath;
    return (
      <div className={Style.detail_container}>
        <Head>
          <title key="title" >
            {data.name} - {SITE_NAME}
          </title>
          <meta name="keywords" content={(data?.tags || []).join(",")} />
          <meta
            name="description"
            content={data.description || data.content.substr(0, 100)}
          />
          <meta property="og:title" content={`${data.name} - ${SITE_NAME}`} />
          <meta property="og:url" content={postLink} />
        </Head>
        <div className={Style.main}>
          <div className={Style.content}>
            <Post data={data} currentUser={undefined} />
            <div className={Style.ads}>
              <ins
                className="adsbygoogle"
                style={{
                  display: "block",
                  textAlign: "center",
                  height: "100px",
                }}
                data-ad-layout="in-article"
                data-ad-format="fluid"
                data-ad-client="ca-pub-6887995008287565"
                data-ad-slot="2469914690"
              ></ins>
            </div>
            <section className={Style.comments}>
              <GiscusComment />
            </section>
          </div>
          <div className={Style.sidebar}>
            <Operator operator={data.operator} />
            <LatestArticles list={latestArticleList} />
          </div>
        </div>
      </div>
    );
  }
}

export async function getStaticProps(context) {
  const id = context.params.id;
  // 获取当前文章
  const res = await Request.getChecklessArticle(id);
  const data = FormatBlog(res.data);
  // 获取最近的5篇文章
  const latestArticleListRes = await Request.getLatestArticleListByOperator(
    data.operatorId
  );
  const latestArticleList = latestArticleListRes.data.data.map(
    (dt) => FormatListBlog(dt)
  );
  return {
    props: { data, id, latestArticleList },
  };
}

export async function getStaticPaths() {
  const res = await Request.getArticleList({
    pageNo: 0,
    pageSize: 9999,
  });

  const blogs = res.data.data.map((dt) => FormatListBlog(dt));
  const paths = blogs.map((blog) => ({
    params: { id: blog.id },
  }));

  return {
    paths,
    fallback: true, // false or 'blocking'
  };
}

export default withRouter(Blog);
