import React, { memo } from 'react';
import classnames from 'classnames';
import style from './style.module.scss';

const EazyWin = props => {
  const className = classnames(style['eazy-win'], {
    [style['eazy-win-dark']]: props.theme === 'dark',
  });
  
  return (
    <div className={className}>
      <div className={style["eazy-win-header"]}>
        <div className={style["win-controll-container"]}>
          <div className={classnames(style["win-controll"], style["win-controll-close"])}></div>
          <div className={classnames(style["win-controll"], style["win-controll-min"])}></div>
          <div className={classnames(style["win-controll"], style["win-controll-full"])}></div>
        </div>
      </div>
      <div className={style["eazy-win-body"]}>{props.children}</div>
    </div>
  );
};

export default memo(EazyWin);
