import React, { memo, useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import remarkMath from "remark-math";
import rehypeKatex from "rehype-katex";
import rehypeRaw from "rehype-raw";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { a11yDark } from "react-syntax-highlighter/dist/cjs/styles/prism";
import EazyWin from "../vwin-eazy-win";
import classnames from "classnames";
import Style from "./style.module.scss";
import ImageViewer from "../ImageViewer";

const MarkdownViewer = (props) => {
  const [load, setLoad] = useState(false);

  useEffect(() => {
    setLoad(true);
  }, []);

  return (
    <div className={classnames(Style.marked)}>
      <ReactMarkdown
        className={"markdown-body"}
        children={props.content}
        remarkPlugins={[remarkGfm, remarkMath, remarkMath, rehypeKatex]}
        rehypePlugins={[rehypeRaw]}
        components={{
          code: ({ node, inline, className, children, ...reset }) => {
            const match = /language-(\w+)/.exec(className || "");
            return !inline && match ? (
              <SyntaxHighlighter
                children={String(children).replace(/\n$/, "")}
                style={a11yDark}
                language={match[1]}
                PreTag="div"
                {...reset}
              />
            ) : (
              <code className={className} {...reset}>
                {children}
              </code>
            );
          },
          img: ({ node, ...reset }) =>
            load ? (
              <EazyWin theme="dark">
                <ImageViewer thumbnail={`${reset.src}!/fw/850`} {...reset}/>
              </EazyWin>
            ) : (
              <img {...reset} src={`${reset.src}!/fw/850`}/>
            ),
        }}
      />
    </div>
  );
};

export default memo(MarkdownViewer);
