import React from "react";
import Markdown from "~/components/markdown";
import View from "~/components/view";
import dayjs from "dayjs";
import Style from "./style.module.scss";
import cn from "classnames";
import { Divider } from "antd";
import { Popover } from "antd";
import { withRouter } from "next/router";
import { getHost } from "~/utils";
import QRCode from "~/components/QRCode";
import { Button } from "antd";
@withRouter
class Post extends React.Component {
  getTime = () => {
    // 相差一天的显示日期， 否则显示n小时前
    const createDay = dayjs(this.props.data.createTime);
    return <span>{createDay.format("YYYY年MM月DD日，dddd，HH:mm") || ""}</span>;
 
  };

  showOriginalText = () => {
    const { data } = this.props;
    if (!data.creativeType || data.creativeType === "original") {
      return true;
    }
    return false;
  };

  getContent = () => {
    const { data } = this.props;
    if (!data) {
      return null;
    }
    if (data.type === "html") {
      return (
        <div
          className="braft-output-content"
          dangerouslySetInnerHTML={{ __html: data.content }}
        ></div>
      );
    }
    return <Markdown content={data.content || ""} />;
  };

  render() {
    const { data, currentUser, router } = this.props;
    if (!data) {
      return <div />;
    }
    const postLink = getHost() + router.asPath;
    return (
      <div className={Style.post_container}>
        <div className={Style.post}>
          <div className={Style.post_content}>
            <div className={Style.post_header}>
              <h1 className={Style.name} title={data.name}>
                {data.name}
              </h1>
              <div className={Style["base-info"]}>
                <span className={Style.item}>
                  <i className={cn("iconfont", Style["icon-time"])}></i>
                  <span>这篇文章发表于&nbsp;{this.getTime()}</span>
                  <Divider type="vertical" />
                  {/* <span>
                    更新于&nbsp;
                    {dayjs(this.props.data.updateTime).format("YYYY-MM-DD")}
                  </span>
                  <Divider type="vertical" /> */}
                  {/* <View data={data.viewCount} /> */}
                </span>
                <div>
                  <Popover
                    placement="bottomRight"
                    arrowPointAtCenter
                    trigger="click"
                    content={<QRCode content={postLink} />}
                  >
                    <Button type="link" size="small">
                      分享
                    </Button>
                  </Popover>
                </div>
              </div>
            </div>
            {data.headerImg ? (
              <img
                className={Style.post_headerImg}
                src={`${data.headerImg}!/fw/850`}
                alt={data.name}
                loading="lazy"
              ></img>
            ) : null}
            <div className={Style.detail_body}>
              <div className={Style.content}>
                {this.getContent()}
                {data.url ? (
                  <a href={data.url}>
                    原文地址:{data.url}
                  </a>
                ) : null}
              </div>
            </div>
            {/* <div className={Style.operations}> */}
            {/* <Operations likeCount={data.likeCount} sourceID={data.id} /> */}
            {/* </div> */}

            {this.showOriginalText() ? (
              <div className={Style.post_notice}>
                <div>本文链接: <a href={postLink}>{postLink}</a> </div>
                <div>关于博主: 评论和私信会在第一时间回复</div>
                <div>版权声明: 本博客所有文章除特别声明外，均采用 <a href="https://creativecommons.org/licenses/by-nc-nd/4.0/" target="_blank">BY-NC-SA</a> 许可协议。转载请注明出处！</div>
              </div>
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}

export default Post;
